<template>
  <el-dialog title="修改节假日信息" :visible.sync="dialogRestEdit.show">
    <el-form :model="formresteditData" label-width="120px" :rules="formresteditRules" ref="formedit">
      <el-form-item label="日期" prop="setdate">
        <el-date-picker
          type="date"
          clearable
          placeholder="选择日期"
          v-model=" formresteditData.setdate "
          align="right"
          :disabled="true"
        ></el-date-picker>
      </el-form-item>
      <el-form-item label="日期类型" prop="datetypevalue">
        <el-select clearable v-model = " formresteditData.datetypevalue" placeholder="选择日期类型">
          <el-option
            v-for="item of datetype"
            :key="item.value"
            :label="item.label"
            :value="item.value"
          ></el-option>
        </el-select>
      </el-form-item>
    </el-form>
    <div slot="footer" class="dialog-footer">
      <el-button @click="dialogRestEdit.show = false">取消</el-button>
      <el-button type="primary" @click="dialogFormEdit('formedit')">提交</el-button>
    </div>
  </el-dialog>
</template>
<script>
import axios from 'axios'
export default {
  name: 'UsersEdit',
  props: {
    dialogRestEdit: Object,
    formresteditData: Object
  },
  data() {
    return {
      formresteditRules: {  //修改表规则
        datetypevalue: [{ required: true, message: '日期类型不能为空', trigger: 'blur' }]
      },
      datetype: [{
        value: "1",
        label: "工作日"
      },
      {
        value: "2",
        label: "周末"
      },{
        value: '3',
        label: '节假日'
      }]
    }
  },
  methods:{
    dialogFormEdit(formedit) {
      if(this.formresteditData.datetypevalue == "1"){
        this.formaddData.datetype = "工作日"
      }
      else if(this.formresteditData.datetypevalue == "2"){
        this.formresteditData.datetype = "周末"
      }
      else{
        this.formresteditData.datetype = "节假日"
      }
      this.$refs[formedit].validate((valid) => {
        if (valid) {
          this.$axios({
            url: '/api/holiday/update',   //发送更新节假日请求
            method: 'post',
            data: {
              'id': parseInt(this.formresteditData.id),
              'setdate': this.formresteditData.setdate,
              'datetypevalue': parseInt(this.formresteditData.datetypevalue),
              'datetype': this.formresteditData.datetype
            },
            headers:{
              'Authorization': 'Bearer '+localStorage.getItem('token')
            }
          }).then(res => {
            this.$message({
              type:"success",
              message:"修改信息成功"
            })
            this.dialogRestEdit.show = false
            this.$emit('update')
          })
          this.$refs[formedit].resetFields()
        } else {
          console.log('error submit!!')
          return false
        }
      })
    }
  }
}
</script>



