<template>
  <el-dialog title="新增管理员信息" :visible.sync="dialogAdminAdd.show">
    <el-form :model="formaddData" label-width="100px" :rules="formaddRules" ref="formadd">
      <el-form-item label="工号" prop="username">
        <el-input type="text" v-model=" formaddData.username "></el-input>
      </el-form-item>
    </el-form>
    <div slot="footer" class="dialog-footer">
      <el-button @click="dialogAdminAdd.show = false">取消</el-button>
      <el-button type="primary" @click="dialogFormAdd('formadd')">提交</el-button>
    </div>
  </el-dialog>
</template>
<script>
import axios from 'axios'
export default {
  name: 'AdminAdd',
  props: {
    dialogAdminAdd: Object,
    userData: Array
  },
  data() {
    return {
      formaddData: {   //新增表名
        username: ''
      },
      formaddRules: {  //新增表规则
        username: [{ required: true, message: '工号不能为空', trigger: 'blur' }]
      },
    }
  },
  methods:{
    dialogFormAdd(formadd) {
      this.$refs[formadd].validate((valid) => {
        if (valid) {
          let formaddData ={
            "formaddData":JSON.stringify(this.formaddData)
          }
          this.$axios({
            url: '/api/users/add/manager',     //发送添加管理员请求
            method: 'post',
            data: {
              'username':this.formaddData.username
            },
            headers:{
              'Authorization': 'Bearer '+localStorage.getItem('token')
            }
          }).then(res => {
            this.$message({
              type:"success",
              message:"添加信息成功"
            })
            this.dialogAdminAdd.show = false
            this.$emit('update')
          })
          this.$refs[formadd].resetFields()
        } else {
          console.log('error submit!!')
          return false
        }
      })
    }
  }
}
</script>



