<template>
  <el-dialog title="新增人员信息" :visible.sync="dialogUserAdd.show">
    <el-form :model="formaddData" label-width="120px" :rules="formaddRules" ref="formadd">
      <el-form-item label="工号" prop="username">
        <el-input type="text" v-model=" formaddData.username "></el-input>
      </el-form-item>
      <el-form-item label="姓名" prop="name">
        <el-input type="text" v-model=" formaddData.name "></el-input>
      </el-form-item>
      <el-form-item label="密码" prop="password">
        <el-input type="text" v-model=" formaddData.password "></el-input>
      </el-form-item>
      <el-form-item label="是否固定车贴" prop="status">
        <el-select clearable v-model = " formaddData.status " placeholder="选择车贴">
          <el-option
            v-for="item of status"
            :key="item.value"
            :label="item.label"
            :value="item.value"
          ></el-option>
        </el-select>
      </el-form-item>

    </el-form>
    <div slot="footer" class="dialog-footer">
      <el-button @click="dialogUsersAdd.show = false">取消</el-button>
      <el-button type="primary" @click="dialogFormAdd('formadd')">提交</el-button>
    </div>
  </el-dialog>
</template>
<script>
import axios from 'axios'
export default {
  name: 'UsersAdd',
  props: {
    dialogUserAdd: Object
  },
  data() {
    return {
      formaddData: {   //新增表名
        username: '',
        name: '',
        password: '',
        status: ''
      },
      formaddRules: {  //新增表规则
        username: [{ required: true, message: '工号不能为空', trigger: 'blur' }],
        name: [{ required: true, message: '姓名不能为空', trigger: 'blur' }],
        password: [{ required: true, message: '密码不能为空', trigger: 'blur' }],
        status: [{ required: true, message: '是否固定车贴不能为空', trigger: 'blur' }]
      },
      status: [{
        value: "1",
        label: "固定车贴"
      },
      {
        value: "0",
        label: "普通"
      }]
    }
  },
  methods:{
    dialogFormAdd(formadd) {
      this.$refs[formadd].validate((valid) => {
        if (valid) {
          let formaddData = {
            "formaddData":JSON.stringify(this.formaddData)
          }
          this.$axios({
            url: '/api/users/add/normaluser',   //发送添加用户请求
            method: 'post',
            data: {
              'username':this.formaddData.username,     //工号
              'name': this.formaddData.name,
              'password': this.formaddData.password,
              'status': parseInt(this.formaddData.status)
            },
            headers:{
              'Authorization': 'Bearer '+localStorage.getItem('token')
            }
          }).then(res => {
            this.$message({
              type:"success",
              message:"添加信息成功"
            })
            this.dialogUserAdd.show = false
            this.$emit('update')
          })
          this.$refs[formadd].resetFields()
        } else {
          console.log('error submit!!')
          return false
        }
      })
    }
  }
}
</script>



