<template>
  <el-dialog title="新增节假日信息" :visible.sync="dialogRestAdd.show" width="500px">
    <el-form :model="formaddData" label-width="120px" :rules="formaddRules" ref="formadd">
      <el-form-item label="日期" prop="setdate">
        <el-date-picker type="date" clearable placeholder="选择日期" v-model=" formaddData.setdate " align="right"></el-date-picker>
      </el-form-item>
      <el-form-item label="日期类型" prop="datetypevalue">
        <el-select clearable v-model = " formaddData.datetypevalue" placeholder="选择日期类型">
          <el-option
            v-for="item of datetype"
            :key="item.value"
            :label="item.label"
            :value="item.value"
          ></el-option>
        </el-select>
      </el-form-item>
    </el-form>
    <div slot="footer" class="dialog-footer">
      <el-button @click="dialogRestAdd.show = false">取消</el-button>
      <el-button type="primary" @click="dialogFormAdd('formadd')">提交</el-button>
    </div>
  </el-dialog>
</template>
<script>
import axios from 'axios'
export default {
  name: 'RestAdd',
  props: {
    dialogRestAdd: Object
  },
  data() {
    return {
      formaddData: {   //新增表名
        setdate: '',
        datetypevalue: '',
        datetype: ''
      },
      formaddRules: {  //新增表规则
        setdate: [{ required: true, message: '日期不能为空', trigger: 'blur' }],
        datetypevalue: [{ required: true, message: '日期类型不能为空', trigger: 'blur' }]
      },
      datetype: [{
        value: "1",
        label: "工作日"
      },
      {
        value: "2",
        label: "周末"
      },{
        value: '3',
        label: '节假日'
      }]
    }
  },
  methods:{
    dialogFormAdd(formadd) {
      if(this.formaddData.datetypevalue == "1"){
        this.formaddData.datetype = "工作日"
      }
      else if(this.formaddData.datetypevalue == "2"){
        this.formaddData.datetype = "周末"
      }
      else{
        this.formaddData.datetype = "节假日"
      }
      // console.log(this.formaddData.datetype)
      this.$refs[formadd].validate((valid) => {
        if (valid) {
          let formaddData = {
            "formaddData":JSON.stringify(this.formaddData)
          }
          this.$axios({
            url: '/api/holiday/insertSelective',   //发送添加节假日请求
            method: 'post',
            data: {
              'setdate': this.formaddData.setdate,
              'datetypevalue': parseInt(this.formaddData.datetypevalue),
              'datetype': this.formaddData.datetype
            },
            headers:{
              'Authorization': 'Bearer '+localStorage.getItem('token')
            }
          }).then(res => {
            this.$message({
              type:"success",
              message:"添加信息成功"
            })
            this.dialogRestAdd.show = false
            this.$emit('update')
          })
          this.$refs[formadd].resetFields()
        } else {
          console.log('error submit!!')
          return false
        }
      })
    }
  }
}
</script>



