<template>
  <el-dialog title="修改人员信息" :visible.sync="dialogUserEdit.show">
    <el-form :model="formeditData" label-width="120px" :rules="formeditRules" ref="formedit">
      <el-form-item label="工号" prop="username">
        <el-input type="text" v-model=" formeditData.username " :disabled="true"></el-input>
      </el-form-item>
      <el-form-item label="是否固定车贴" prop="status">
        <el-select clearable v-model = " formeditData.status " placeholder="选择车贴">
          <el-option
            v-for="item of status"
            :key="item.value"
            :label="item.label"
            :value="item.value"
          ></el-option>
        </el-select>
      </el-form-item>
    </el-form>
    <div slot="footer" class="dialog-footer">
      <el-button @click="dialogUsersEdit.show = false">取消</el-button>
      <el-button type="primary" @click="dialogFormEdit('formedit')">提交</el-button>
    </div>
  </el-dialog>
</template>
<script>
import axios from 'axios'
export default {
  name: 'UsersEdit',
  props: {
    dialogUserEdit: Object,
    formeditData: Object
  },
  data() {
    return {
      formeditRules: {  //修改表规则
        username: [{ required: true, message: '工号不能为空', trigger: 'blur' }],
        status: [{ required: true, message: '是否固定车贴不能为空', trigger: 'blur' }]
      },
      status: [{
        value: "1",
        label: "固定车贴"
      },
      {
        value: "0",
        label: "普通"
      }]
    }
  },
  methods:{
    dialogFormEdit(formedit) {
      this.$refs[formedit].validate((valid) => {
        if (valid) {
          let formeditData = {
            "formeditData":JSON.stringify(this.formeditData)
          }
          this.$axios({
            url: '/api/users/update/normaluser',   //发送更新用户请求
            method: 'post',
            data: {
              'username':this.formeditData.username,     //工号
              'status': parseInt(this.formeditData.status)
            },
            headers:{
              'Authorization': 'Bearer '+localStorage.getItem('token')
            }
          }).then(res => {
            this.$message({
              type:"success",
              message:"修改信息成功"
            })
            this.dialogUserEdit.show = false
            this.$emit('update')
          })
          this.$refs[formedit].resetFields()
        } else {
          console.log('error submit!!')
          return false
        }
      })
    }
  }
}
</script>



