<template>
  <div>
    <!-- 头部 -->
    <div class="header">
      <div class="title">系统设置</div>
    </div>
    <!-- 侧边栏 -->
    <div class="aside">
      <img class="img-aside" src="@/assets/images/插图.png">
      <img class="img-title" src="@/assets/images/logo.png"/>
      <router-link to="/adminmain">
        <div class="icon">
          &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<i class="el-icon-edit-outline">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;加班登记</i>
        </div>
      </router-link>
      <div class="icon2">
        &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<i class="el-icon-setting">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;系统设置</i>
      </div>
      <div class="user">
        <img class="user-img" src="@/assets/images/user-img.jpg">
        <p class="user-name">{{this.name}}</p>
      </div>
      <el-button class="exit" @click="handleExit()">退出登录</el-button>
    </div>

    <!-- 主页面 -->
    <div class="main">
      <div class="content">
        <div class="setting">
          <el-tabs v-model="activeName">
            <el-tab-pane label="权限设置" name="first">
              <div class="admin-setting" v-if="isSuperadmin()">
                <el-col>
                  <div>
                    <el-form :inline="true">
                      <el-form-item style="float: left" label="查询管理员:">
                        <el-input v-model="adminUser" placeholder="请输入管理员姓名"></el-input>
                      </el-form-item>
                      <el-form-item style="float: right">
                        <el-button type="primary" icon="el-icon-edit-outline" @click="handleAdminAdd()" >添加</el-button>
                      </el-form-item>
                    </el-form>
                  </div>
                  <div class="main-table">
                    <el-table :data="searchAdmininfo(adminUser)" border style="width: 100%" height="370">
                      <el-table-column type="selection" width="100" align="center"></el-table-column>
                      <el-table-column type="index" label="序号" align="center" width="150"></el-table-column>
                      <el-table-column label="姓名" prop="name" align="center" width="160">
                        <template slot-scope="scope">
                          <span>{{scope.row.name}}</span>
                        </template>
                      </el-table-column>
                      <el-table-column label="工号" prop="username" align="center" width="160">
                        <template slot-scope="scope">
                          <span>{{scope.row.username}}</span>
                        </template>
                      </el-table-column>
                      <el-table-column label="是否翔安固定车贴" prop="status" align="center" width="160">
                        <template slot-scope="scope">
                          <span>{{scope.row.status}}</span>
                        </template>
                      </el-table-column>
                      <el-table-column label="用户类型" prop="role" align="center" width="160">
                        <template slot-scope="scope">
                          <span>{{scope.row.role}}</span>
                        </template>
                      </el-table-column>
                      <el-table-column label="操作" align="center">
                        <template slot-scope="scope">
                          <el-button size="mini" type="danger" @click="handleAdminDelete(scope.$index, scope.row)">删除</el-button>
                        </template>
                      </el-table-column>
                    </el-table>
                  </div>
                </el-col>
                <AdminAdd :dialogAdminAdd="dialogAdminAdd" @update="getUsers"></AdminAdd>
              </div>
              <div class="admin-setting" v-if="!isSuperadmin()">
                <el-col>
                  <div>
                    <el-form :inline="true">
                      <el-form-item style="float: left" label="查询管理员信息:">
                        <el-input v-model="adminUser" placeholder="请输入管理员姓名"></el-input>
                      </el-form-item>
                    </el-form>
                  </div>
                  <div class="main-table">
                    <el-table :data="searchAdmininfo(adminUser)" border style="width: 100%" height="370">
                      <el-table-column type="selection" width="100" align="center"></el-table-column>
                      <el-table-column type="index" label="序号" width="100" align="center"></el-table-column>
                      <el-table-column label="姓名" prop="name" align="center"></el-table-column>
                      <el-table-column label="工号" prop="username" align="center" ></el-table-column>
                      <el-table-column label="是否翔安固定车贴" prop="status" align="center" ></el-table-column>
                      <el-table-column label="用户类型" prop="role" align="center"></el-table-column>
                    </el-table>
                  </div>
                </el-col>
              </div>
            </el-tab-pane>
            <el-tab-pane label="人员设置" name="second">
              <div class="admin-setting">
                <el-col>
                  <div>
                    <el-form :inline="true">
                      <el-form-item style="float: left" label="查询人员信息:">
                          <el-input v-model="keyUsers" placeholder="请输入姓名"></el-input>
                      </el-form-item>
                      <el-form-item style="float: right">
                        <el-button
                          type="primary"
                          icon="el-icon-edit-outline"
                          @click="handleUsersAdd()"
                        >添加</el-button>
                      </el-form-item>
                    </el-form>
                  </div>
                  <div class="main-table">
                    <el-table :data="searchUserinfo(keyUsers)" border style="width: 100%" height="370">
                      <el-table-column type="selection" width="60" align="center"></el-table-column>
                      <el-table-column type="index" label="序号" align="center" width="135"></el-table-column>
                      <el-table-column label="姓名" prop="name" align="center" width="160">
                        <template slot-scope="scope">
                          <span>{{ scope.row.name}}</span>
                        </template>
                      </el-table-column>
                      <el-table-column label="工号" prop="username" align="center" width="160">
                        <template slot-scope="scope">
                          <span>{{ scope.row.username}}</span>
                        </template>
                      </el-table-column>
                      <el-table-column label="是否翔安固定车贴" prop="status" align="center" width="160">
                        <template slot-scope="scope">
                          <span>{{ scope.row.status}}</span>
                        </template>
                      </el-table-column>
                      <el-table-column label="用户类型" prop="role" align="center" width="160">
                        <template slot-scope="scope">
                          <span>{{ scope.row.role}}</span>
                        </template>
                      </el-table-column>
                      <el-table-column label="操作" align="center">
                        <template slot-scope="scope">
                          <el-button size="mini" type="danger"
                            @click="handleDelete(scope.$index, scope.row)">删除</el-button>
                          <el-button size="mini"
                            @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
                        </template>
                      </el-table-column>
                    </el-table>
                  </div>
                </el-col>
                <UsersAdd :dialogUserAdd="dialogUserAdd" @update="getUsers"></UsersAdd>
                <UsersEdit :dialogUserEdit="dialogUserEdit" @update="getUsers" :formeditData="formeditData"></UsersEdit>
              </div>
            </el-tab-pane>
            <el-tab-pane label="休息日设置" name="third">
              <div class="admin-setting">
                <el-col>
                  <div>
                    <el-form :inline="true">
                      <el-form-item label="输入月份查看节假日信息:">
                        <el-date-picker  width="20" type="month" placeholder="请输入月份"  v-model="restmonth"></el-date-picker>
                      </el-form-item>
                      <el-form-item style="float: right">
                        <el-button type="primary" icon="el-icon-edit-outline" @click="handleRestAdd()" >新增</el-button>
                      </el-form-item >
                    </el-form>
                  </div>
                  <div class="main-table">
                    <el-table :data="searchRest(restmonth)" border style="width: 100%" height="314">
                      <el-table-column type="selection" width="60" align="center"></el-table-column>
                      <el-table-column type="index" label="序号" align="center" width="135"></el-table-column>
                      <el-table-column label="日期" prop="setdate" align="center" width="250">
                        <template slot-scope="scope">
                          <span>{{ scope.row.setdate | dateformat('YYYY-MM-DD')}}</span>
                        </template>
                      </el-table-column>
                      <el-table-column label="日期类型" prop="datetype" align="center" width="250">
                        <template slot-scope="scope">
                          <span>{{ scope.row.datetype}}</span>
                        </template>
                      </el-table-column>
                      <el-table-column label="操作" align="center">
                        <template slot-scope="scope">
                          <el-button size="mini" type="danger"
                            @click="handleRestDelete(scope.$index, scope.row)">删除</el-button>
                          <el-button size="mini"
                            @click="handleRestEdit(scope.$index, scope.row)">编辑</el-button>
                        </template>
                      </el-table-column>
                    </el-table>
                  </div>
                  <P>注意：
                      本系统默认周一至周五为工作日，周六周日为周末，当遇到节假日调休时，设置需要调休的时间，例如，5月1日-5月3日
                      原为工作日，但正逢五一假期，因此新增5月1日为节假日，新增5月2日和5月3日为周末，设置原为周日的5月5日为工作日。
                    </P>
                </el-col>
                <RestAdd :dialogRestAdd="dialogRestAdd" @update="getRest"></RestAdd>
                <RestEdit :dialogRestEdit="dialogRestEdit" @update="getRest" :formresteditData="formresteditData"></RestEdit>
              </div>
            </el-tab-pane>
          </el-tabs>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import axios from 'axios'
  import AdminAdd from './components/AdminAdd'
  import UsersAdd from './components/UsersAdd'
  import UsersEdit from './components/UsersEdit'
  import RestAdd from './components/RestAdd'
  import RestEdit from './components/RestEdit'
  export default {
    name: 'SettingMain',
    components: {
      AdminAdd,
      UsersAdd,
      UsersEdit,
      RestAdd,
      RestEdit
    },
    data() {
      return {
        activeName: 'first',
        adminData:[],
        userData: [],
        restData: [],
        adminUser:'',
        name: localStorage.getItem('name'),
        keyUsers: '',
        restmonth: '',
        dialogAdminAdd: { show: false },   //是否显示新增管理员信息
        dialogUserAdd: {  show: false },  //是否显示新增人员信息
        dialogRestAdd: { show: false },  //是否显示新增节假日信息
        dialogUserEdit: { show: false },   //是否显示编辑用户信息
        dialogRestEdit: { show: false },   //是否显示编辑节假日信息
        formeditData: {    //修改表名
          username: '',
          status: ''
        },
        formresteditData: {    //修改节假日表名
          setdate: '',
          datetypevalue: ''
        }
      }
    },
    methods: {
      handleAdminAdd () {     //是否显示新增管理员信息
        this.dialogAdminAdd.show = true
      },
      handleUsersAdd () {     //是否显示新增人员信息
        this.dialogUserAdd.show = true
      },
      handleExit() {
        this.$router.replace('/')
      },
      handleRestAdd () {     //是否显示新增节假日信息
        this.dialogRestAdd.show = true
      },
      handleEdit (index,row) {   //显示编辑用户信息
        this.dialogUserEdit.show = true
        this.formeditData = {
          username: row.username,
          status: row.status,
          id: row.id
        }
      },
      handleRestEdit (index,row) {   //显示编辑节假日信息
        this.dialogRestEdit.show = true
        this.formresteditData = {
          setdate: row.setdate,
          datetypevalue: row.datetype,
          id: row.id
        }
      },
      isSuperadmin(){
        if(localStorage.getItem('role') == 3)
          {return true}
        else if(localStorage.getItem('role')==1)
          {return false}
      },
      handleAdminDelete(index,row) {  // 删除管理员
        this.$confirm('此操作将删除此管理员，是否继续？','警告',{
          confirmButtonText: '确定',
          cancelButtonText: '取消'
        }).then(() => {
          this.$axios({
            url: '/api/users/delete/manager',
            method: 'delete',
            headers: {
              'Authorization': 'Bearer '+localStorage.getItem('token')
            },
            data:{
              'username': row.username
            }
          }).then(res =>{
            this.$message({
              type:"success",
              message:"删除信息成功"
            })
            this.getUsers()    //删除数据，更新视图
          })
        }).catch(()=>{})
      },
      handleDelete(index,row) {  // 删除用户信息
        this.$confirm('此操作将删除此条用户信息，是否继续？','警告',{
          confirmButtonText: '确定',
          cancelButtonText: '取消'
        }).then(() => {
          this.$axios({
            url: '/api/users/delete/user',
            method: 'delete',
            headers: {
              'Authorization': 'Bearer '+localStorage.getItem('token')
            },
            data: {
              'username': row.username
            }
          }).then(res =>{
            this.$message({
              type:"success",
              message:"删除信息成功"
            })
            this.getUsers()   //删除数据，更新视图
          })
        }).catch(()=>{})
      },
      handleRestDelete(index,row) {  // 删除节假日信息
        this.$confirm('此操作将删除此条休息日设置，是否继续？','警告',{
          confirmButtonText: '确定',
          cancelButtonText: '取消'
        }).then(() => {
          this.$axios({
            url: '/api/holiday/delete',
            method: 'delete',
            headers: {
              'Authorization': 'Bearer '+localStorage.getItem('token')
            },
            data:{
              'id':parseInt(row.id)
            }
          }).then(res =>{
            this.$message({
              type:"success",
              message:"删除信息成功"
            })
            this.getRest()    //删除数据，更新视图
          })
        }).catch(()=>{})
      },
      searchAdmininfo(adminUser) {
        return this.adminData.filter((admin) => {
          if(admin.name.includes(adminUser)){
            return admin
          }
        })
      },
      searchUserinfo(keyUsers) {
        return this.userData.filter((users) => {
          if(users.name.includes(keyUsers)){
            return users
          }
        })
      },
      searchRest(restmonth){
        return this.restData.filter(item =>{
          if(!restmonth){
            return item
          }
          else if(restmonth){
            const Y1 = new Date(restmonth).getFullYear()
            const M1 = new Date(restmonth).getMonth() + 1
            const Y2 = new Date(item.setdate).getFullYear()
            const M2  = new Date(item.setdate).getMonth() + 1
            if(Y1==Y2 && M1==M2){
              return item
            }
          }
        })
      },
      getRest() {
        this.$axios({
          url: '/api/holiday/getAll',
          method: 'get',
          headers: {
            'Authorization': 'Bearer ' + localStorage.getItem('token')
          }
        }).then(res =>{
          const data = res.data
          this.restData = data
          // console.log(this.restData)
        })
      },
      getUsers() {
        this.$axios({      //获取所有的用户信息
          url: '/api/users/all',  //
          method: 'get',
          headers: {
            'Authorization': 'Bearer ' + localStorage.getItem('token')
          }
        }).then(res =>{
          const msgdata = res.data.data.userInfos
          // console.log(msgdata)
          this.userData = []
          this.adminData = []
          if(msgdata){
            msgdata.forEach(item => {
              // console.log(item)
              var user ={
                name: '',
                username: '',
                role: '',
                status: '',
              }
              user.name = item.name
              if(item.status == 0){
                user.status = '普通'
              }else{
                user.status = '固定车贴'
              }
              user.username = item.username
              if(item.roles){
                item.roles.forEach(i =>{
                  if(i.rid == 1 || i.rid == 3){
                    var admin ={
                      name: '',
                      username: '',
                      status: '',
                      role: ''
                    }
                    admin.name = item.name
                    admin.username = item.username
                    if(item.status == 0){
                      admin.status = '普通'
                    }else if(item.status == 1){
                      admin.status = '固定车贴'
                    }
                    if(i.rid == 1){
                      admin.role = '管理员'
                      user.role = "管理员"
                    }else if(i.rid == 3){
                      admin.role = "超级管理员"
                      user.role = "超级管理员"
                    }
                    this.adminData.push(admin)
                  }
                  else
                    user.role = "普通用户"
                this.userData.push(user)
                })
              }
            })
          }
        })
      }
    },
    mounted(){
      this.getUsers(),
      this.getRest()
    }
  }
</script>
<style lang="stylus" scoped>
  .header
    position: absolute
    top: 0
    right: 0
    left: 200px
    height: 56px
    background: #fff
    .title
      margin-left: 28px
      line-height: 56px
      font-size: 16px
      color: rgba(0,0,0,0.85)
  .aside
    position:absolute
    top: 0
    left: 0
    bottom: 0
    width: 200px
    background: #1B202E
    .img-aside
      position: absolute
      bottom: 0
      width: 200px
    .img-title
      position: absolute
      top: 20px
      left: 27px
    .icon
      position: absolute
      top: 75px
      height: 42px
      width: 100%
      text-align: left
      font-size: 15px
      line-height: 42px
      color: #FFFFFF
    .icon2
      position: absolute
      top: 117px
      height: 42px
      width: 100%
      text-align: left
      font-size: 15px
      color: #FFFFFF
      background-image: linear-gradient(90deg, #59E3FF 0%, #1F9CFF 100%)
      line-height: 42px
    .user
      position: absolute
      bottom: 200px
      left: 70px
      right: 70px
      .user-img
        position: absolute
        width: 60px
        height: 60px
        border-radius: 100px
        width: 100%
    .user-name
      position: absolute
      top: 60px
      left: -10px
      width: 80px
      height: 15px
      font-size: 15px
      line-height: 15px
      text-align: center
      color: #fff
    .exit
      position: absolute
      bottom: 50px
      left: 51px
      opacity: 0.4
      color: #fff
      background-color: transparent
  .main
    position: absolute
    top: 56px
    left: 200px
    bottom: 0
    right: 0
    background: #eee
    .content
      position: absolute
      top: 24px
      left: 24px
      bottom: 24px
      right: 24px
      background: #fff
      .setting
        margin: 10px 20px
        .admin-setting
          margin: 10px
          .page
            margin-top: 10px
          .main-table
            text-align: center

</style>



